:root {
  --font-body: Kumbbh Sans, sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --darkGray: hsl(0, 0%, 63%);
  --veryDarkGray: hsl(0, 0%, 27%);
  --verydarkdesaturatedblue: hsl(238, 29%, 16%);
  --softorange: hsl(14, 88%, 65%);
  --verydarkgrayishblue: hsl(237, 12%, 33%);
  --darkgrayishblue: hsl(240, 6%, 50%);
  --softviolet: hsl(273, 75%, 66%);
  --softblue: hsl(240, 73%, 65%);
  --lightgrayishblue: hsl(240, 5%, 91%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: var(--font-body);
  background-repeat: no-repeat;
  background-image: linear-gradient(to bottom right, var(--softviolet), var(--softblue));
  font-size: 0.8125rem;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
}
@media (max-width: 26.66em) {
  body {
    background-image: linear-gradient(var(--softviolet), var(--softblue));
  }
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  padding: 0 1.5rem;
  max-width: 69.375rem;
  margin: 0 auto;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
    transform: translate(0, 0);
  }
}

.container {
  padding-left: 1.875rem !important;
  padding-right: 1.875rem !important;
}

.a-card {
  background: var(--white);
  border-radius: 2rem;
  width: 100%;
}

.illustration {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-right: 1.875rem;
  max-height: 7.5rem;
}
.illustration__img {
  max-height: 12.5rem;
  position: relative;
  transform: translateY(-6.25rem);
}
.illustration__shadow {
  max-height: 13.75rem;
  position: relative;
  transform: translateY(-11.875rem);
  left: 0.9375rem;
}

.accordion {
  position: relative;
}